header {
  display: block;
  width: 100%;
  margin: 0 auto;
  padding: 0 10px;
  background-color: black;
  height: 150px; }

.wrap {
  position: relative;
  display: block;
  width: 100%;
  height: 100%;
  margin: 0 auto;
  overflow: auto; }
  .wrap .text {
    color: white;
    width: 100%;
    float: right;
    text-align: center;
    overflow: auto;
    align-content: center;
    position: absolute;
    top: 50%;
    left: 50%;
    margin-right: -50%;
    font-family: 'Chakra Petch', sans-serif;
    font-size: 32px;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%); }
  .wrap .logo {
    display: inline-block;
    width: 200px;
    height: 10%;
    position: absolute;
    -webkit-transform: translateY(-50%);
    transform: translateY(-50%); }
    .wrap .logo img {
      width: 100%;
      margin: 0;
      display: inline-block; }

.button-style {
  width: 100%;
  text-align: center;
  justify-content: center;
  font-size: 70px;
  font-family: 'Chakra Petch', sans-serif; }

button {
  background: rgba(26, 90, 40, 0.9);
  color: white; }

.button-output {
  width: 100%;
  width: 400px;
  height: 400px;
  border-radius: 50%;
  border: 2px solid white;
  background: url(/freight-farms-app/static/media/earth.e8b334bc.jpg);
  background-size: cover;
  background-size: 900px;
  margin-top: 15px;
  margin-left: auto;
  margin-right: auto;
  margin-bottom: 11%; }

.output {
  display: inline-block;
  justify-content: center;
  margin-top: 20%;
  text-align: center;
  vertical-align: middle;
  font-size: 30px;
  word-wrap: normal;
  padding: 25px;
  color: #fff;
  border-radius: 50%;
  line-height: 2; }

.main {
  width: 100%;
  text-align: center;
  justify-content: center;
  padding-top: 5%;
  font-family: 'Chakra Petch', sans-serif;
  font-size: 50px;
  color: white; }

body {
  background: url(/freight-farms-app/static/media/lettuce.f58747de.jpeg);
  padding: 0;
  height: 100%;
  background-size: cover;
  background-repeat: no-repeat;
  background-attachment: fixed;
  background-color: #ffff00; }

